as y contenedores.")]),_c('td',[_vm._v("Impermeabilidad, gran diversidad, reutilizable.")]),_c('td',[_vm._v("Inflamable, costoso y difícil eliminación.")])]),_c('tr',[_c('td',[_vm._v("Papel")]),_c('td'),_c('td',[_vm._v("Bolsas.")]),_c('td',[_vm._v("Bajos costos y fácil eliminación.")]),_c('td',[_vm._v("Muy frágil, sensible a la humedad y al calor.")])]),_c('tr',[_c('td',[_vm._v("Vidrio")]),_c('td'),_c('td',[_vm._v("Botellas, frascos y botellones.")]),_c('td',[_vm._v("Visibilidad del contenido, fácil estibado y reciclable.")]),_c('td',[_vm._v("Frágil a los golpes, pesado y voluminoso.")])])])])])])])
